import numberStep from './numberStep';
import editPusrchase from './editPusrchase';
export default {
	components: {
		numberStep,
		editPusrchase
	},
	props: [
		'disabled',
		'itemsData',
		'indexNum',
		'selectExist',
		'dataList',
		'deleteMoveShow'
	],
	data() {
		return {
			dataLists: this.dataList,
		};
	},
	watch: {
		dataList: {
			handler(a, b) {
				console.log('----------------dataList--------------')
				this.dataLists = a;
			},
			deep: true
		},
	},
	created() {},
	methods: {
		tapEditPusrchase (e) {
			console.log(e, '用户行为');
			this.dataLists.dataList = e;
			this.$emit('tap-PurchaseNumber', this.dataLists);
		},
		tapNumberStep (e) {
			console.log(e);
			this.dataLists.dataList.map((items, index)=>{
				if (index === [1]) {
					items = e[0];
				}
			});
			this.$emit('tap-PurchaseNumber', this.dataLists);
		}
	}
};
